
@charset "UTF-8";
.cartcontrol[data-v-0e99dc4d] {
  font-size: 0;
}
.cartcontrol .cart-decrease[data-v-0e99dc4d] {
    display: inline-block;
    padding: 6px;
    -webkit-transition: all 0.4s linear;
    transition: all 0.4s linear;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    opacity: 1;
    /* 小球刚刚enter的状态和小球leave-active状态 */
}
.cartcontrol .cart-decrease .inner[data-v-0e99dc4d] {
      display: inline-block;
      line-height: 24px;
      font-size: 24px;
      color: #00a0dc;
      -webkit-transition: all 0.4s linear;
      transition: all 0.4s linear;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
}
.cartcontrol .cart-decrease.move-enter[data-v-0e99dc4d], .cartcontrol .cart-decrease.move-leave-active[data-v-0e99dc4d] {
      -webkit-transition: all 0.4s linear;
      transition: all 0.4s linear;
      -webkit-transform: translate3d(24px, 0, 0);
              transform: translate3d(24px, 0, 0);
      opacity: 0;
}
.cartcontrol .cart-decrease.move-enter .inner[data-v-0e99dc4d], .cartcontrol .cart-decrease.move-leave-active .inner[data-v-0e99dc4d] {
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg);
}
.cartcontrol .cart-count[data-v-0e99dc4d] {
    display: inline-block;
    vertical-align: top;
    width: 12px;
    padding-top: 6px;
    line-height: 24px;
    text-align: center;
    font-size: 10px;
    color: #93999f;
}
.cartcontrol .cart-add[data-v-0e99dc4d] {
    display: inline-block;
    padding: 6px;
    line-height: 24px;
    font-size: 24px;
    color: #00a0dc;
}



