@charset "UTF-8";
body, html {
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif; }

.clearfix {
  display: inline-block; }
  .clearfix:after {
    display: block;
    height: 0;
    line-height: 0;
    clear: both;
    visibility: hidden; }

/* 1px边框 */
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
  .border-1px::after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7); } }

@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
  .border-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5); } }

@font-face {
  font-family: 'icomoon';
  src: url("../fonts/icomoon.eot?zdcvz1");
  src: url("../fonts/icomoon.eot?zdcvz1#iefix") format("embedded-opentype"), url("../fonts/icomoon.ttf?zdcvz1") format("truetype"), url("../fonts/icomoon.woff?zdcvz1") format("woff"), url("../fonts/icomoon.svg?zdcvz1#icomoon") format("svg");
  font-weight: normal;
  font-style: normal; }

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

